<template>
  <div>
    <gallery :images="images" :index="index" @close="index = null"></gallery>
    <v-layout row wrap class="thumbnails">
      <v-flex xs6 sm6 md4 lg3 xl2 pa-2
        class="image"
        v-for="(image, imageIndex) in images"
        :key="imageIndex"
        @click="index = imageIndex"
        :style="{ backgroundImage: 'url(' + getImageThumbnail(image) + ')' }"
      >
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import VueGallery from 'vue-gallery';

export default {
  components: {
    'gallery': VueGallery
  },
  name: 'PhotoGallery',
  props: {
    images: Array,
    name: String
  },
  data() {
    return {
      index: null,
      hasSelectedIndex: null
    }
  },
  methods: {
    getImageThumbnail(image) {
      let slugs = image.split('/')
      return slugs.map((slug, index) => {
        return (index+1) === slugs.length  ?  `thumbnails/${slug}` : slug
      }).join("/")
    } 
  }
}
</script>

<style lang="scss" scoped>
  .thumbnails {
    justify-content: space-evenly;
  }

  .image {
    display: inline-block;
    min-width: 500px;
    min-height: 350px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border: 2px solid white;

    &:hover {
      cursor: pointer;
    }
  }
</style>